$fonts-path: "../assets/fonts" !default;
/* poppins-300 - latin */
// Fonts generated - https: //google-webfonts-helper.herokuapp.com/fonts/poppins?subsets=latin

@font-face {
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 300;
	src: local(''),
		url('#{$fonts-path}/poppins-v20-latin-300.woff2') format('woff2'),
		/* Chrome 26+, Opera 23+, Firefox 39+ */
		url('#{$fonts-path}/poppins-v20-latin-300.woff') format('woff');
	/* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

